//  axios  ajax
import axios from 'axios';

var $http = axios.create({
	// url
  baseURL: '/api',
  timeout: 5000,
  headers: {'X-Custom-Header': 'foobar'}
});

// 
function getTopics(options){
	// 
	var newsOpitons = Object.assign({
    category:"news_hot",
    count:20,
	},options);

	return $http({
		url:'/',
		// 
		params:newsOpitons,
	});
}


// 
function getTopicData(id){
	return $http.get('/topic/'+id);
}




// 
export {getTopics}


